import Vue from 'vue'
import Router from 'vue-router'
//import HelloWorld from '@/components/HelloWorld'
//import Drug from '@/components/Drug'
//import Cf from '@/components/Cf'
Vue.use(Router)
const HelloWorld = ()=> import('@/components/HelloWorld')
const Drug =()=>import('@/components/Drug')
const Cf =()=>import('@/components/Cf')
const routes = [
    {
      path:'',
      redirect:'/Drug'
    },
    {
      path: '/Hello',
      name: 'HelloWorld',
      component: HelloWorld
    },
    // {
    //   path: '/me',
    //   name: 'me',
    //   component: MyTable
    // },
    {
      path: '/Drug',
      name: 'Drug',
      component: Drug
    }
    ,
    {
      path: '/Cf',
      name: 'cf',
      component: Cf
    }
  ]
export default new Router({
  mode:'history',
  routes: routes
})



